
const background_star = document.querySelector('.background_star')
const pos_background_star = background_star.getBoundingClientRect()
import starLarge from '../images/SVG/star-large.svg'


let windowWidth = window.innerWidth
let numberOfStar

if (windowWidth>900 ) {
    numberOfStar = 20
}
else{
    numberOfStar = 10
}
for (let i = 0; i < numberOfStar; i++) {
    let star_large = document.createElement('img')
    star_large.src= starLarge
    star_large.classList.add('star_large')
    star_large.style.width=`${Math.random(Math.PI)*40}px`
    star_large.style.position='fixed'
    star_large.style.zIndex = "1"
    star_large.style.top=`${Math.random()*pos_background_star.width}px`
    star_large.style.left=`${Math.random()*pos_background_star.width-50}px`
    background_star.appendChild(star_large)  
    star_large.classList.add('twinkle')
}



