const background_star = document.querySelector('.background_star')
const pos_background_star = background_star.getBoundingClientRect()
import starMin from '../images/SVG/star-min.svg'
let windowWidth = window.innerWidth
let numberOfStar

if (windowWidth>900 ) {
    numberOfStar = 500
}
else{
    numberOfStar = 100
}
for (let i = 0; i < numberOfStar; i++) {
    let star_min = document.createElement('img')
    star_min.src=starMin
    star_min.classList.add('stars')
    star_min.style.width=`${Math.random(Math.PI)*8}px`
    star_min.style.position='fixed'
    star_min.style.zIndex = "1"
    star_min.style.top=`${Math.random()*pos_background_star.width*Math.PI}px`
    if (windowWidth>1100 ) {
        star_min.style.left=`${Math.random()*pos_background_star.width-50}px`
    }
    else{
        star_min.style.left=`${Math.random()*pos_background_star.width-10}px`
    }
    
    background_star.appendChild(star_min) 
}